section {
	display: flex;
	align-items: center;
	justify-content: center;
	height: auto;
	min-height: 100vh;
	width: 100%;
	border-bottom: 1px solid lightgrey;
	scroll-snap-align: start;
}


section:nth-child(even) {
	flex-direction: row-reverse;
}

section img,
section div {
	height: auto;
	width: 100%;
}

section img {
	width: 50%;
}


@media screen and (max-width: 900px) {
	section,
	section:nth-child(even) {
		flex-direction: column;
	}

	section img {
		height: auto;
		width: auto;
		max-width: 100vw;
	}
}
